import React, { memo } from "react";
import { DynamicHeader, ScreenWrapper } from "@true/ui";
import { translate, useNavigationParams } from "@true/core";
import { defaultParams } from "@policy/screens/SearchScreen/index";
import { IRequestSearchPolicyParams } from "@policy/store/policies/functions";
import { FilterComp } from "@policy/components/FilterComp";

export interface SearchFilterProps {
  params: IRequestSearchPolicyParams;
  setParams: (value: any) => void;
}

export const SearchFilter = memo(() => {
  const {params, setParams} = useNavigationParams<SearchFilterProps>();

  return (
    <ScreenWrapper>
      <DynamicHeader title={translate('policy.filter')} />
      <FilterComp
        items={[
          'collection',
          'folder',
          'storage',
          'receiver',
          'doctype',
          'created_by',
          'created_date',
          'effective_date',
          'expired_date',
        ]}
        params={params}
        setParams={setParams}
        defaultParams={defaultParams}
      />
    </ScreenWrapper>
  );
});
